@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  font-family: 'Mina', sans-serif;
}

#app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

#app > div {
  width: 250px;
  margin: 10px;
}

table {
  border-collapse: collapse;
}

th, td {
  border: 1px solid rgba(69, 115, 152, 0.99);
  padding: 5px;
}

.App {
  width: 1200px;
  min-height: 100vh;
  margin-left: auto;
  margin-right: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  /* align-items: center; */
  font-size: 1.2rem;
}

.load-spinner {
  width: 200px;
  object-fit: contain;
  height: 200px;
}


